}
.informacion {
  width: 100%;
  background-color: aliceblue;
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  border-radius: 2rem;
  /* grid-auto-flow: dense; */
}
.formulario {
  width: 100%;
  /* background-color: red; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
}
.itemPrincipal {
  grid-column: span 1;
  grid-row-start: 1;
  grid-row-end: 4;
  width: 100%;
  background-color: white;
  /* border-radius: 2rem; */
  /* margin: 1px; */
  padding: 1px;
  justify-content: center;
}
.itemSecundario {
  grid-column: span 1;
  height: -webkit-fit-content;
  height: fit-content;
  background-color: white;
  /* border-radius: 2rem; */
  margin: 0.1rem;
  padding: 0.4rem;
}
.itemLargo {
  grid-column: span 2;
  grid-row-start: 2;
  grid-row-end: 3;
  width: 100%;
  background-color: white;
  border-radius: 2rem;
  /* margin: 1px; */
  padding: 2px;
  justify-content: center;
}
.itemTitulo {
  font-weight: bolder;
  display: flex;
  justify-content: center;
  align-items: center;
}
.itemInfo {
  background-color: white;
  border-radius: 2rem;
  /* margin: 2px; */
  padding: 1px;
  justify-content: right;
  align-items: center;
  border-color: #0095eb solid;
  overflow: hidden;
}
.b {
  margin: 0;
  width: -webkit-fit-content;
  width: fit-content;
  /* padding: auto; */
  font-size: bold;
  /* margin-right: 5px; */
  white-space: nowrap;
}

.entrada {
  padding: 5px;
  display: grid;
  grid-gap: 2px;
  gap: 2px;
  grid-template-rows: auto auto;
  margin: 0;
  width:-webkit-fit-content;
  width:fit-content
}
.entrada label {
  font-weight: bolder;
  grid-column: span 1;
  text-align: left;
}
.entrada input {
  grid-column:span 1;
  grid-row-start: 2;
  grid-row-end: 3;
  text-align: left;
  width:-webkit-fit-content;
  width:fit-content;
  max-width:150px;
}

